<template>
  <div class="Source">
    <!-- <Step :stepList="stepList"></Step> -->
    <el-steps
      :active="active - 0"
      finish-status="success"
      align-center
    >
      <el-step
        v-for="(item,index) in stepList"
        :key="index"
        :title="item.val"
      ></el-step>
    </el-steps>
    <el-tabs
      tab-position="left"
      v-model="active"
    >
      <el-tab-pane
        v-for="(item,index) in stepList"
        :key="index"
        :name="item.id"
        :label="item.val"
      >
        <!-- {{item.val}} -->
        <el-form
          ref="addFormRef"
          :model="formObj"
          :rules="$rules.common"
          status-icon
        >
          <!-- 姓名 -->
          <el-form-item
            v-for="(item,index) in formList"
            :key="index"
            :label="item.lable"
            :prop="item.prop"
          >
            <el-input
              class="form-input"
              v-model="item.name"
            ></el-input>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
    <el-button
      style="margin-top: 12px;"
      @click="next"
      v-if="active!=='3'"
    >下一步</el-button>
    <el-button
      style="margin-top: 12px;"
      v-else
    >完成</el-button>
  </div>
</template>

<script>
// import Step from "@/components/step.vue";
export default {
  // components: {
  //   Step,
  // },
  data() {
    return {
      active: "0",
      stepList: [
        { id: "0", val: "基本信息" },
        { id: "1", val: "学籍信息" },
        { id: "2", val: "联系方式" },
        { id: "3", val: "档案信息" },
      ],
      formList: [
        { lable: "姓名", prop: "name", name: "", type: "input" },
        { lable: "性别", prop: "sex", sex: "", type: "radio" },
        { lable: "身份证号", prop: "idCardNo", idCardNo: "", type: "input" },
        { lable: "出生日期", prop: "birth", birth: "", type: "input" },
        { lable: "政治面貌", prop: "politics", politics: "", type: "input" },
        { lable: "民族", prop: "nation", nation: "", type: "input" },
        { lable: "生源所在地", prop: "domicile", domicile: "", type: "input" },
      ],
    };
  },
  computed: {
    formObj() {
      let obj = {};
      this.formList.forEach((i) => {
        obj[i.prop] = i[i.prop];
      });
      console.log(obj, 1111111);
      return obj;
    },
  },
  methods: {
    next() {
      if (this.active * 1 < 3) {
        this.active = this.active * 1 + 1 + "";
      }
    },
  },
};
</script>

